.navbar{
    background: rgb(255, 255, 254);
    height: 5rem;
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
}

.logo2 {
    object-fit: cover;
    height: 3rem;
    width: 4rem;
}

.desktopMenuListItem {
    margin: 1rem;
    cursor: pointer;
    
}

.desktopMenuListItem:hover {
    color: rgb(255, 0, 191);
    padding-bottom: 0.5rem;
    border-bottom: 3px solid rgb(255, 0, 191);
}

.desktopMenuImg {
    object-fit: cover;
    cursor: 1rem;
    width: 1rem;
    margin: 1rem;

}

.desktopMenuBtn {
    background: white;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
}
.desktopMenuBtn:hover {
    box-shadow: 0 0 6px rgb(16, 16, 16),
    0 0 0 6px rgb(249, 2, 187);
}

.active {
    color: rgb(255, 0, 191);
    padding-bottom: 0.5rem;
    border-bottom: 3px solid rgb(255, 0, 191);
}

.mobMenu {
    display: none;
    object-fit: cover;
    height: 1.8rem;
}
.mobMenu:hover {
    box-shadow: 0 0 6px rgb(16, 16, 16),
    0 0 0 6px rgb(249, 2, 187);
}

.navMenu {
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40,40,40);
    border-radius: 1rem;
}

.listItem {
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30,30,30);
}

@media screen and (max-width:720px){
    .mobMenu {
        display: flex;

    }
    .desktopMenu {
        display: none;
    }
    .desktopMenuBtn {
        display: none;
    }
    
}