#intro {
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
}

.bg {
    position: absolute;
    top: 8.5rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 70vh;
}

.introContent {
    height: 100vh;
    width: 100vw;
    margin-left: 2rem;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
   
}

.introName {
    color: rgb(255, 0, 191);
}

.introPara {
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
   
}



.btnImg {
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}

.containerBtn {
    display: flex;
    gap: 1px;
}

.resume {
    background: rgb(247, 115, 192);
    margin: 0.5rem;
    margin-left: 6rem;
    padding: 0.85rem 4.5rem;
    font: medium;
    border: none;
    border-radius: 2rem;
}
.resume:hover {
    box-shadow: 0 0 6px rgb(16, 16, 16),
    0 0 0 6px rgb(249, 2, 187);
}
@media screen and (max-width:480px) {
    .bg {
        right: -20vw;
    }
    
    
}