#contactPage {
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contactPageTitle {
    font-size: 3rem;
    margin-bottom: 1rem;
}

.companyDesc {
    font-weight: 300;
    font-size: 1rem;
}

.companyImgs {
    margin: 1rem 0;
    display: flex;
    flex-wrap: wrap;
}

.companyImg {
    object-fit: cover;
    width: 25;
    min-width: 9rem;
    margin: auto;
    padding: 0.25rem;
}

#company {
    padding: 2rem;
}

.contactDesc {
    padding: 1rem;
    font-size: medium;
    font-weight: 300;
}

.contactForm {
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;

}

.name, .email, .msg {
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem;
    color: white;
    border: none;
    border-radius: 0.5rem;
    background: rgb(249, 135, 202);
}

.submitBtn {
    background: rgb(254, 85, 184);
    border: none;
    box-shadow: 0 0 5px rgb(237, 6, 145);
    border-radius: 0.5rem;
    margin: 2rem;
    padding: 0.75rem 3.5rem;
}
.submitBtn:hover {
    box-shadow: 0 0 6px rgb(16, 16, 16),
    0 0 0 8px rgb(249, 2, 187);
}

.links {
    display: flex;
    flex-wrap: wrap;
}

.link:hover {
    box-shadow: 0 0 6px rgb(16, 16, 16),
    0 0 0 6px rgb(249, 2, 187);
}

.link {
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75;
}